//parcel src/index.html
window.dom = {
    create(string) {//Áî®‰∫éÂàõÂª∫ËäÇÁÇπ
        const container = document.createElement//ÂàõÂª∫‰∏Ä‰∏™ÂÖÉÁ¥†
            ("template");// ÂÆπÁ∫≥‰ªªÊÑèÂÖÉÁ¥†Ôºå‰∏ç‰ºöÂú®È°µÈù¢‰∏≠ÊòæÁ§∫:template
        container.innerHTML = string.trim();//ÂÖÉÁ¥†ÁöÑÂÜÖÂÆπÂ∞±ÊòØÊàëÂÜôÁöÑÂÜÖÂÆπ
        //trimÂèñÊ∂à2ËæπÁöÑÁ©∫Ê†º
        return container.content.firstChild;
    },
    after(node, node2) {//Áî®‰∫éÊñ∞Â¢ûÂºüÂºüÔºånode2ÊîæÂà∞nodeÁöÑÂêéÈù¢
        node.parentNode.insertBefore(node2, node.nextSibling);
        //ÂÖàÊâæÂà∞nodeÁöÑÁà∏Áà∏ÔºåÊäänode2Êü•Âà∞nodeÁöÑ‰∏ã‰∏Ä‰∏™ËäÇÁÇπÁöÑÂâçÈù¢
    },
    before(node, node2) {//Áî®‰∫éÊñ∞Â¢ûÂì•Âì•
        node.parentNode.insertBefore(node2, node);
    },
    append(parent, node) {//Áî®‰∏éÊñ∞Â¢ûÂÑøÂ≠ê
        parent.appendChild(node);
    },
    wrap(node, parent) {//Áî®‰∫éÊñ∞Â¢ûÁà∏Áà∏
        dom.before(node, parent)//ÊääÊñ∞Â¢ûÁöÑËäÇÁÇπÊîæÂà∞‰πãÂâçËäÇÁÇπÁöÑÂâçÈù¢
        dom.append(parent, node)//Êää‰πãÂâçËäÇÁÇπÊîæÂà∞Êñ∞Â¢ûËäÇÁÇπÁöÑÈáåÈù¢
    },//‰πãÂâçÁöÑËäÇÁÇπÂ∞±‰ºöÊ∂àÂ§±
    remove(node) {//Áî®‰∫éÂà†Èô§ËäÇÁÇπ
        node.parentNode.removeChild(node)
        //ËÆ©Ëøô‰∏™ËäÇÁÇπÁöÑ‰∏äÁ∫ßËäÇÁÇπÂà†Èô§Â≠êËäÇÁÇπ
        return node
    },
    empty(node) {//Áî®‰∫éÂà†Èô§Âêé‰ª£
        //ÁÆÄÂçïÁöÑÂÜôÊ≥ï,Êó†Ê≥ïËé∑ÂèñÂºïÁî®Ôºönode.innerHTML = ''
        const array = []
        let x = node.firstChild
        while (x) {
            array.push(dom.remove(node.firstChild))
            x = node.firstChild
        } return array
    },
    attr(node, name, value) {//ÈáçËΩΩ
        if (arguments.length === 3) {
            node.setAttribute(name, value)
        } else if (arguments.length === 2) {
            return node.getAttribute(name)
        }
    },
    text(node, string) {
        if ('innerText' in node) {
            node.innerText = string
        } else {
            node.textContent = string
        }
    },
    html(node, string) {
        node.innerHTML = string
    },
    find(selector, scope) {//Ëé∑ÂèñÊ†áÁ≠æÊàñÊ†áÁ≠æ‰ª¨
        //const testdiv=dom.find('#test')[0]
        return (scope || document).querySelectorAll(selector)
    },
    style(node, name, value) {//‰øÆÊîπstyle
        if (arguments.length === 3) {//divÈáåÊ∑ªÂä†color:red
            // dom.style(div, 'color', 'red')
            node.style[name] = value
        } else if (arguments.length === 2) {
            if (typeof name === 'string') {//Ëé∑ÂèñcolorÂÖÉÁ¥†Èáå‰∏úË•ø
                // dom.style(div, 'color')
                return node.style[name]
            } else if (name instanceof Object) {//divÈáåÊ∑ªÂä†color:red
                // dom.style(div, {color: 'red'})
                const object = name
                for (let key in object) {
                    node.style[key] = object[key]
                }
            }
        };
    },
    each(nodeList, fn) {//ÈÅçÂéÜÊâÄÊúâËäÇÁÇπ
        for (let i = 0; i < nodeList.length; i++) {
            fn.call(null, nodeList[i])
        }
    },
}